/*
Style Sheet for Portal Component
*/

.moralWrapper {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(255, 255, 255, 0.9);
    z-index: 1000;
}

.moralWrapper {
    display: flex;
    justify-content: center;
    align-items: center;
}

/* Style for Close Button */

button.close {
    position: absolute;
    top: 20px;
    right: 20px;
    fill: black;
}

button.close {
    width: 30px;
    height: 30px;
    background-color: transparent;
    outline: none;
    border: 0px;
    cursor: pointer;
}

button.close:hover{
    background-color:transparent; 
}